<template>
    <div class='main'>
        <div class='form'>
            <el-form :inline="true" :model="form" class="demo-form-inline">
                <el-form-item class='search'>
                    <el-input placeholder=""class="input-with-select" v-model='form.search'>
                        <el-button slot="append" @click='search()'></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker  type="daterange" align="right" unlink-panels range-separator="-"
                                     start-placeholder="" end-placeholder="" @change="search()"
                                     style="width: 100%;" value-format="yyyy-MM-dd HH:mm:ss"  v-model='form.date'>
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </div>
        <div class='table'>
            <el-table :data="tableData" style="width: 100%" :max-height="screenHeight" >
                <el-table-column prop="" label=""  align="center">
                    <template slot-scope='scope'>
                        <img :src="scope.row.wx_head">
                    </template>
                </el-table-column>
                <el-table-column prop="name" label=""  align="center"></el-table-column>
                <el-table-column prop="phone" label="" align="center"></el-table-column>
                <el-table-column prop="server" label=""   align="center"></el-table-column>
                <el-table-column prop="reg_time" label=""   align="center"></el-table-column>
                <el-table-column label=""  align="center">
                    <template slot-scope='scope'>
                        <span class='ellipsis'>{{scope.row.skill}}</span>
                        <el-button type='text' size='small' @click='details(scope.row)'></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="address" label=""  align="center">
                    <template slot-scope='scope'>
                       <el-button type='primary' size='small' @click='Authentication(scope.row)'></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog title="" class='details' :visible.sync="detailsDialogVisible" width="40%" center>
            <span>{{detailsContent}}</span>
        </el-dialog>
        <component is='paging' :pageCount='pageCount' :currentChange='currentChange'></component>
    </div>
</template>
<script>
    import { unverified,auditTns,skillDetail,techSearch } from '@/server/serverData'
    import paging from '@/views/common/paging'
    export default{
        data(){
            return{
                currentPage:1,//
                pageCount:1, //
                form:{
                    search:'',
                    date:''
                },
                tableData:[],
                screenHeight:window.innerHeight-370,
                detailsDialogVisible:false,
                detailsContent:''
            }
        },
        computed:{
            ApplyTime(){  /** ,,, **/
                return this.form.date instanceof Array ? {
                    startTime:this.form.date[0],
                    endTime: this.form.date[1]
                } :{
                    startTime:'',
                    endTime: ''
                }
            },

        },
        components:{
            paging
        },
        methods:{

          /**  **/
          async init(){  //currentPage ,,
               let res = await techSearch({
                   name:this.form.search,
                   startTime:this.ApplyTime.startTime,
                   endTime:this.ApplyTime.endTime,
                   page:this.currentPage
               })
               if(res.data.code==1){
                   this.tableData=res.data.data.list
                   this.pageCount=res.data.data.rows
               }else{
                   this.tableData=[];
                   this.pageCount=1
               }
        },

          /**  **/
          async details(row){
              this.detailsDialogVisible=true
              let res = await skillDetail({id:row.id})
              if(res.data.code==1){
                  this.detailsContent=res.data.data
              }
          },
          /**  **/
          Authentication(row){
                 /** page-1 **/
                 if(this.pageCount > 1 && this.currentPage == this.pageCount && this.tableData.length <= 1)  this.currentPage-=1;

                 this.$confirm('?','',{
                     confirmButtonText:'',
                     cancelButtonText:'',
                     type:'warning'
                 }).then(()=>{
                       this.request(row.id,row.cert)
                 }).catch(()=>{})
          },
          /**  **/
          async request(id,cert){
             let res = await auditTns({id:id,cert:cert})
             if(res.data.code==1){
                 let that=this
                 this.init(that.currentPage)
                 this.$message({message:res.data.msg,type:'success',duration:1500,onClose(){

                     that.$store.commit('CHANGE_NUMBER','engineer')
                 }})
             }else {
                 this.$message.error(res.data.msg)
             }
          },
           /**  **/
           search(){
               /** 1, **/
               this.currentPage=1;
               this.pageCount=1;
               this.init()
           },
          /**  **/
          currentChange(e){
            this.currentPage=e;
            this.init()
          }
        },
        mounted(){
        this.init()
            var that=this
            window.onresize = () => {
                return (() => {
                    that.screenHeight = window.innerHeight - 370
            })()
            }
        }
    }
</script>
<style scoped lang='scss' rel="stylesheet/scss">
    .form{
        padding: 20px 0 20px 30px;
        margin-bottom: 0;
        /deep/ .el-form-item__content{
            .el-input-group__append, .el-input-group__prepend{
                border: none;
                background-color: #3498E9;
                color: white;
                padding: 0 30px;
            }

        }
       .el-form-item{
            margin-bottom: 0 !important;
        }
        .search{
            margin-right: 40px;
        }
    }
    .table{
        /deep/ .el-table th, .el-table tr{
            background-color: #D7E1E6;
            .ellipsis{
                width: 70px;
                height: 100%;
                line-height: 100%;
                font-size: 12px;
                display: inline-block;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
        }
        /deep/ .el-table__row{
            td{
                 .cell{
                    font-size: 12px;
                }
            }
        }
        img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
        }
    }
    .paging{
        text-align: center;
        margin-top: 30px;
    }
    .details{
        /deep/ .el-dialog__body{
            height: 200px;
            overflow: auto;
        }
    }
</style>